* {
  padding:0;
  margin:0;

}

.app {
  font-family: Arial, Helvetica, sans-serif
}
body{
  background-image: linear-gradient(to right top, #051937, #0e2d5a, #184380, #225ba8, #2c73d2);
  background-color:white;
  background-size:contain;
  width:100vw;
  height:100vh;
}
header {
    background-color:#333;
    height:10vh;
    text-align:center;
    line-height:10vh;
    color:white;
    font-size:30px;
    opacity:0.4;
}

.converter{
    background-color:#2C73D2;
    height:40vh;
    width:80vw;
    margin: 100px auto;
    border-radius: 20px 20px 0 0;
    border-bottom:5px solid #D5A419;
    
    
}

.converter .form input{
    width:15vw;
    height:4vw;
    border-radius:10px;
    border:0px solid black;
    font-size:30px;
    margin:0 0px;
    /* position:relative; */
}

.converter p {
    position:absolute;
    color:white;
    right:12vw;
    top:60vh;
}

.form{
    position:absolute;
    left:50%;
    transform:translateX(-50%);
    width:70vw;
    margin:70px auto;
}

.form label{
    position:absolute;
    color:white;
    margin-left:15px;
    margin-top:-25px;
}

.form img{
    position:absolute;
    margin-left:65px;
    margin-top:-35px;
    width:35px;
    height:35px;
    border-radius:500px;
}

.form p{
    /* position:absolute; */
    color:white;
}

.resultTable{
    position:absolute;
    top:50vh;
    left:50%;
    transform:translate(-50%);
    font-size:20px;
    color:white;

}



.converter .form select {
    width:15vw;
    height:4vw;
    border-radius:10px;
    border:0px solid black;
    font-size:30px;
    margin:0 10px;
}

.converter .form select option{
    background-color:#D5A419;
    color:white;
    border-radius:10px;
}

.converter .form button {
    background-color: #D5A419;
    cursor:pointer;
    color:white;
    width:15vw;
    height:4vw;
    border-radius:10px;
    border:0px solid black;
    font-size:30px;
    margin:0 10px; 
}

.converter .form button.arrows{
    width:10px;
    background-color:transparent;
    transform:translateX(-75%);
}



@media(max-width: 900px){
    .converter .form button{
        background-color:#D5A419;
    }

    
    .converter .form input,.converter .form select,.converter .form button{
        width:100%;
        height:15%;
        text-align:center;
        margin-right:0;
        margin-left:0;
        margin-bottom:10px;
    }

    .form label{
        display:none;
    }

    .converter{
        height:400px;
        /* position:relative; */
    }

    .resultTable{
        /* width:100%; */
       font-size:16px;
       display:block;
       /* width:270px; */
       min-width:270px;
       top:450px;
       transform:translateX(-50%)
       /* min-width:300px; */
    }

    .converter p {
        position:absolute;
        color:white;
        right:15%;
        top:525px;
    }

    .converter .form button.arrows{
        position:absolute;
        /* margin-left:0; */
        right:-25px;
        top:65px;
    }


}

@media(max-width: 562px){
    .converter{
        width:90vw;
    }
    
}
